.form__container {
    text-align: center;
    background-color: var(--color-bg-vari);
    padding-top: 75px;
    width: 300px;
    height: 225px;
    border-radius: 100%;

    animation-name: example;
    animation-duration: 10s;
    animation-iteration-count: infinite;
    animation-direction: alternate;

}

@keyframes example {
    0% {background: var(--color-primary-vari)}
    100% {background: var(--color-primary);}

  }

.reply__container {
    text-align: center;
    padding-top: 1rem;
}

section {
    margin-top: 3rem;
}

label {
    display: block;
    padding: 0.1rem;
}

input {
    margin-right: 0.7rem;
    padding: 0.3rem;
}

.btn {
    display: block;
    margin: auto;
    margin-top: 2rem;
}



